import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid           from '@material-ui/core/Grid';
import Typography     from '@material-ui/core/Typography';
import FacebookIcon   from '@material-ui/icons/Facebook';
import LinkedInIcon   from '@material-ui/icons/LinkedIn';
import TwitterIcon    from '@material-ui/icons/Twitter';
import InstagramIcon    from '@material-ui/icons/Instagram';
import Link           from '@material-ui/core/Link';

const useStyles = makeStyles(theme => ({
    root: {
      flexGrow: 1,
          },
     }));
     const gridContainer_style={height:"60px",backgroundColor:"black"};

function Footer()
{
    const classes = useStyles();
    return(
    <div className={classes.root}>
    <Grid container spacing={0} style={gridContainer_style}>
      <Grid item xs={6} >
      <Typography variant="subtitle1" style={{paddingTop:"20px",color:"white"}}>
      &copy; All Rights Reserved to IT Solutions
      </Typography>   
      </Grid>
      <Grid item xs={6} >
      <div style={{paddingTop:"10px",textAlign:"center"}}>
      <Link href="#"><FacebookIcon className="footerIcon_circle"/></Link>
      <Link href="#"><LinkedInIcon className="footerIcon_circle"/></Link>
      <Link href="#"><TwitterIcon className="footerIcon_circle"/></Link>
      <Link href="#"><InstagramIcon className="footerIcon_circle"/></Link>
      </div>
      </Grid>
      </Grid>
      </div>
    )
}

export default Footer;
