import React          from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar         from '@material-ui/core/AppBar';
import Toolbar        from '@material-ui/core/Toolbar';
import Typography     from '@material-ui/core/Typography';
import IconButton     from '@material-ui/core/IconButton';
import MenuIcon       from '@material-ui/icons/Menu';
import Link           from '@material-ui/core/Link';

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(1),
    backgroundColor:"#ff9999",
  },
  title: {
    flexGrow: 1,
    color:"steelblue"
  },
}));

const links_style = {textDecoration: "none",color: "black",padding: "0px 30px 0px",marginRight:"10px",fontSize:"22px",fontFamily:"Arial"};


function NavBar() {
  const classes = useStyles();


  return (
    <div className={classes.root}>
      <AppBar position="fixed" style={{backgroundColor:"white",boxShadow: '5px'}}>
        <Toolbar >
          <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu">
            <MenuIcon />
          </IconButton>
          <Typography variant="h6" className={classes.title}>
            IT SOLUTIONS
          </Typography>
          <Link  style={links_style} href="#about_id" >ABOUT</Link>
          <Link  style={links_style} href="#">PROJECTS</Link>
          <Link  style={links_style} href="#map_id">MAP</Link>
          <Link  style={links_style} href="#contact_id">CONTACT</Link>
        </Toolbar>
      </AppBar>
    </div>
  );
}
export default NavBar;