import React          from 'react';
import Heading        from  './Heading';
import { makeStyles } from '@material-ui/core/styles';
import TextField      from '@material-ui/core/TextField';
import Card           from '@material-ui/core/Card';
import CardContent    from '@material-ui/core/CardContent';
import Grid           from '@material-ui/core/Grid';
import Typography     from '@material-ui/core/Typography';
import EmailIcon      from '@material-ui/icons/Email';
import PhoneIcon      from '@material-ui/icons/Phone';
import HomeIcon       from '@material-ui/icons/Home';
import Button         from '@material-ui/core/Button';

const useStyles = makeStyles(theme => (
  {root_form: {'& > *': {margin: theme.spacing(1),width: 200,marginBottom:"40px"},},
   root_grid: {flexGrow: 1,},
   root_card: {minWidth: 275,},
   title:     {fontSize: 14,},
   pos:       {marginBottom: 12,},
  }));
const icons = {fontSize:"50px"};
let formButton_style = {borderRadius:"20px",backgroundColor:"palegreen",fontFamily:"Arial",fontSize:"20px"};

function Contact()
{
  const [value, setValue] = React.useState('');
  const handleChange = event => {
  setValue(event.target.value);
    };


  const classes = useStyles();

  return (
      <div id="contact_id">
      <Heading heading="CONTACT US"/>

         <div className={classes.root_grid}>
         <Grid container spacing={0}>

         <Grid item xs={1}>
         
         </Grid>

         <Grid item xs={5}>
         <Card className={classes.root_card} variant="outlined" style={{backgroundColor:"#F8F8F8",margin:"50px",border:"2px solid darkgray"}}>
         <CardContent >
         <Typography className={classes.title} color="textSecondary" gutterBottom>
          Contact Form
        </Typography>
         <form className={classes.root_form} noValidate autoComplete="off">
         <TextField id="fullname_id" label="Name" />
         <TextField id="email_id" label="Email" />
         <TextField id="outlined-multiline-flexible" label="Comments" multiline rowsMax="4" value={value} onChange={handleChange} variant="outlined"/>
         <Button variant="contained" href="#contained-buttons" style={formButton_style}>Submit</Button>
         </form>
         </CardContent>
         </Card>
         </Grid>

         <Grid item xs={1}>
       
         </Grid>

         <Grid item xs={4}>
         <EmailIcon className="icon_circle" style={icons}/>
         <Typography variant="h5">Itsolutions@gmail.com</Typography>
         <br/>
         <PhoneIcon className="icon_circle" style={icons}/>
         <Typography variant="h5">0313243704444</Typography>
         <br/>
         <HomeIcon className="icon_circle" style={icons}/>
         <Typography variant="h5">Street ABC, Karachi Pakistan</Typography>
         </Grid>

         <Grid item xs={1}>
      
         </Grid>
         
         </Grid>
         </div>

      </div>
         );
}
export default Contact;



