import React          from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid           from '@material-ui/core/Grid';
import Heading        from  './Heading';
import About_pic2     from './About_pic2.jpg';
import Typography     from '@material-ui/core/Typography';

const useStyles = makeStyles(theme => ({
    root: {
      flexGrow: 1,
          },
     }));
   const About_pic2_style =     {marginLeft:"100px",height:"530px", maxWidth:"640px",backgroundImage: `url(${About_pic2})`}; 
   const About_div_text_style = {height:"480px",width:"260px",wordWrap: "keepAll",padding:"30px"};
   const h1_style =             {fontSize:"50px",fontFamily:"Arial",color:"#e9f3f4",textAlign:"center",paddingTop:"150px"};
   const Typography_style=      {color:"white",fontSize:"26px",fontFamily:"Arial"};
   
   function About()
{
    
    const classes = useStyles();
    return(<div id="about_id">
    <Heading heading="ABOUT US"/>
    <div className={classes.root}>
    <Grid container spacing={0}>
    <Grid item xs={8} style={{backgroundColor:"#eacecf" }}>
    <div style={About_pic2_style}>
    <h1 style={h1_style}>ABOUT US..</h1>
    </div>
    </Grid> 
    <Grid item xs={4} style={{backgroundColor:"black"}}>
    <div style={About_div_text_style}> 
    <Typography variant="h5" style={Typography_style}>
    Lorem ipsum is a placeholder text commonly used to demonstrate the visual form of a document or a typeface without relying on meaningful content. 
    Lorem ipsum is a placeholder text commonly used to demonstrate the visual form of a document
    </Typography>
    </div>
    </Grid> 
    </Grid>
    </div>
    </div>);
}
export default About;