import React          from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid           from '@material-ui/core/Grid';
import Typography     from '@material-ui/core/Typography';
import Button         from '@material-ui/core/Button';
import Cards          from './Cards';
import Animate        from './Animate';

const useStyles = makeStyles(theme => ({
    root: {
      flexGrow: 1,
      marginTop: "40px"
    },
  }));

  /*******styles**********/
  let Grid_style   = {paddingTop:"50px"}; 
  let Button_style = {borderRadius:"20px",backgroundColor:"palegreen",fontFamily:"Arial",fontSize:"20px"};

  function MainContent() 
  {

    const classes = useStyles();
    return(
        <div className={classes.root}>
        <Grid container spacing={0} style={Grid_style}>

            <Grid item xs={1}>
            </Grid> 

             <Grid item xs={4}>
             <Typography variant="h4"  id="Typo">
             <span id="letter">O</span>ptimism is the faith that leads to achievement. Nothing can be done
             without hope and confidence.     
             </Typography>
             <br/>
             <Button variant="contained" href="#contained-buttons" style={Button_style}>Get Started</Button>
             <Animate color="palegreen" width="80px" height="80px" left="830px" top="95px"/>
             </Grid>  

             <Grid item xs={5}>
             <Cards />
             <Animate color="#ff9999" width="120px" height="120px" left="520px" top="50px"/>
            </Grid> 

             <Grid item xs={2}>
             <Animate color="palegreen"  width="160px"  height="160px"  left="8px"   top="10px"/>
             <Animate color="#ff9999"    width="130px"  height="130px"  left="95px"  top="10px"/>
             <Animate color="palegreen"  width="150px"  height="150px"  left="40px"  top="10px"/>
             </Grid>

       </Grid>
        </div>
        )
  }
  export default MainContent;